import dag from "butterfly-dag";
import pipelineEvents from "./events";
// import Canvas from "../class/DragCanvas.js";
const { Canvas } = dag;
export default function(flowData) {
  let root = document.getElementById("canvas");
  let canvas = new Canvas({
    root: root,
    disLinkable: true, // 
    linkable: true, // 
    draggable: true, // 
    zoomable: true, // 
    moveable: true, // 
    // autoFixCanvas: {
    //
    // enable: true,
    // autoMovePadding: [20, 20, 20, 20] //
    // },
    layout: {
      type: "dagreLayout",
      options: {
        nodeSize: [108, 32],
        rankdir: "LR",
        align: "DL",
        nodesep: 8,
        ranksep: 16,
        controlPoints: false
      }
    }
    // theme: {
    //   edge: {
    //     type: "Manhattan",
    //     arrow: true,
    //     arrowPosition: 1
    //   }
    // }
  });
  canvas.draw(flowData);
  canvas.on("events", data => {
    let { type } = data;
    let f = pipelineEvents[type];
    if (f) {
      f.bind(this)(data);
    }
  });
  return canvas;
}
